//-----------------------|| DASHBOARD - BAJAJ AREA CHART ||-----------------------//

const ChartData = ({extra = []}) => {
    let height = 200;
    
    return {
        type: 'treemap',
        height: height,
        options: {
            colors: ['#1e88e5', '#808080'],
            stroke: {
                curve: 'smooth',
                width: 3
            },
            chart: {
                sparkline: {
                    enabled: true
                },
            },
        },
        series: [
                {
                    name: "Strategy",
                    data: extra
                },
            ],
        }
    };

export default ChartData;
