    const account = useSelector((state) => state.account);
    const theme = useTheme();
    const reqs = `numpy
pandas
`;
    const def = `from AlgorithmImports import *
### <summary>
### In this example we look at the canonical 15/30 day moving average cross. This algorithm
### will go long when the 15 crosses above the 30 and will liquidate when the 15 crosses
### back below the 30.
### </summary>
### <meta name="tag" content="indicators" />
### <meta name="tag" content="indicator classes" />
### <meta name="tag" content="moving average cross" />
### <meta name="tag" content="strategy example" />
class MovingAverageCrossAlgorithm(QCAlgorithm):

    def Initialize(self):

        self.SetStartDate(2015, 1, 1)    #Set Start Date
        self.SetEndDate(2020, 1, 1)      #Set End Date
        self.SetCash(100000)             #Set Strategy Cash
        # Find more symbols here: http://quantconnect.com/data
        self.AddEquity("SPY", Resolution.Daily)
        self.AddEquity("QQQ", Resolution.Daily)

        # create a 15 day exponential moving average
        self.spy = self.STD("QQQ", 15, Resolution.Daily)

        # create a 30 day exponential moving average
        self.qqq = self.STD("SPY", 15, Resolution.Daily)

        self.previous = None


    def OnData(self, data):
        if not self.qqq.IsReady or not self.spy.IsReady:
            return

        # only once per day
        if self.previous is not None and self.previous.date() == self.Time.date():
            return

        # define a small tolerance on our checks to avoid bouncing
        tolerance = 0.005

        # we only want to go long if we're currently short or flat
        try:
            if self.spy.Current.Value/data["SPY"].Price > self.qqq.Current.Value/data["QQQ"].Price *(1 + tolerance):
                self.SetHoldings("QQQ", 0.9)
                self.SetHoldings("SPY", 0)

            elif self.qqq.Current.Value/data["QQQ"].Price > self.spy.Current.Value/data["SPY"].Price *(1 + tolerance):
                self.SetHoldings("QQQ", 0)
                self.SetHoldings("SPY", 0.9)
        except Exception as e: 
            self.SetHoldings("QQQ", 0)
            self.SetHoldings("SPY", 0)
        
        self.previous = self.Time

`;
    const [codet, setCode] = useState(def);
    const [cards, setCards] = useState({});
